  formatNumber = (num) => {
    return num.toString().replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1,')
  }

  //////
  printPDF() {

    let { data, header } = this.props

    let docDefinition = {

      content: [
        this.renderFrom(data, header)
      ],



      styles: {
        tableHeader: {
          bold: true,
          fontSize: 10,
          color: 'black'
        }
      },
      pageOrientation: 'landscape',
      defaultStyle: {
        font: 'THSarabunNew',
        fontSize: 10
      },

    };



    pdfMake.createPdf(docDefinition).open()

  }
